.title {
  @apply .font-raleway .font-normal;
  @apply .text-gray-900 .text-2xl .text-center;
  @apply .leading-tighter .tracking-normal;
  @apply .pb-3 .mb-2;
}

.subtitle {
  @apply .title .font-bold .tracking-widest .pb-8;
}

@screen lg {
  .title,
  .subtitle {
    @apply .text-3xl;
  }
}

.animation-appear {
  animation: appear ease-in-out;
}

.animation-duration-2000 {
  animation-duration: 2000ms;
}

@keyframes appear {
  0% {
    opacity: 0;
  }
  1% {
    display: block;
  }
  100% {
    opacity: 1;
  }
}
